.formbuilder {
  font-size: larger;
}

.formbuilder .formcomponent {
  display: flex;
  height: 3rem;
  margin-top: 0.6rem;
  margin-bottom: 0.8rem;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  justify-content: space-between;
  line-height: 3rem;
  box-shadow: rgba(104, 118, 145, 0.05) 1px 1px 1px;
  cursor: pointer;
  width: 100% !important;
  padding: 0px 1rem;
  border-radius: 0.25rem;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(227, 233, 243);
  border-image: initial;
  background: rgb(255, 255, 255);
  color: #373a3ccf;
}

.formbuilder .formcomponent.btn-primary:active {
  color: #fff;
  background-color: #0E76F4;
  border-color: #0E76F4;
}

.formbuilder .form-builder-group-header .mb-0 {
  width: 100%;
}

.formbuilder .formcomponents .formcomponent {
  padding: 5px 15px 5px 15px;
}

.formbuilder .formcomponent.gu-transit {
  color: #fff;
  background-color: #48a0f8;
  border-color: #48a0f8;
  opacity: 0.6;
}

.formio-dialog .btn-secondary {
  color: #fff;
  background-color: #d5dae0;
  border-color: #d5dae0;
}
