.formbuilder .formio-form {
  border-style: dashed;
  border-color: #1716162b;
  border-width: 1.6px;
  padding: 20px 20px 20px 20px;
}

.formioForm > div {
  width: 100%;
}

.formioForm {
  width: 100%;
}

.radio label,
.radio-inline label,
.checkbox-inline label,
.checkbox label {
  font-weight: 400 !important;
  font-size: small !important;
}

input[type="radio"] {
  display: none;
  margin: 10px;
}

table input[type="radio"] {
  display: block;
  margin: 10px;
}

